"use client";

import { motion } from 'framer-motion';
import { Card } from '../ui/Card';
import { 
  Gamepad2, 
  DollarSign, 
  Users, 
  Shield, 
  Zap, 
  Trophy,
  Target,
  Clock
} from 'lucide-react';

export const FeaturesSection = () => {
  const features = [
    {
      icon: Gamepad2,
      title: 'Valorant Integration',
      description: 'Seamlessly connect your Valorant account and Tracker.gg profile for automatic match verification.',
      color: 'text-red-400'
    },
    {
      icon: DollarSign,
      title: 'Real Money Payouts',
      description: 'Win tournaments and receive instant UPI payments. No waiting, no hassle.',
      color: 'text-green-400'
    },
    {
      icon: Users,
      title: 'Flexible Formats',
      description: 'Choose between intense 1v1 duels or strategic 5v5 team battles.',
      color: 'text-blue-400'
    },
    {
      icon: Shield,
      title: 'Anti-Abuse System',
      description: 'Advanced fraud detection and community-driven moderation ensure fair play.',
      color: 'text-purple-400'
    },
    {
      icon: Zap,
      title: 'Auto-Assignment',
      description: 'Get automatically matched to tournaments based on your rank and preferences.',
      color: 'text-yellow-400'
    },
    {
      icon: Trophy,
      title: 'Tournament Variety',
      description: 'Daily tournaments with different prize pools, entry fees, and skill brackets.',
      color: 'text-orange-400'
    },
    {
      icon: Target,
      title: 'Skill-Based Matchmaking',
      description: 'Compete against players of similar skill level for balanced and exciting matches.',
      color: 'text-pink-400'
    },
    {
      icon: Clock,
      title: '24/7 Availability',
      description: 'Join tournaments anytime, anywhere. No waiting for specific schedules.',
      color: 'text-indigo-400'
    }
  ];

  return (
    <section className="py-20 bg-gradient-to-b from-black/80 to-black/90 backdrop-blur-sm">
      <div className="max-w-7xl mx-auto px-4">
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
          className="text-center mb-16"
        >
          <h2 className="text-4xl md:text-5xl font-bold text-white mb-6">
            Why Choose <span className="bg-gradient-to-r from-purple-400 to-blue-400 bg-clip-text text-transparent">Tournify</span>?
          </h2>
          <p className="text-xl text-gray-300 max-w-3xl mx-auto">
            Experience the future of competitive gaming with our cutting-edge tournament platform
          </p>
        </motion.div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
          {features.map((feature, index) => (
            <Card key={index} delay={index * 0.1}>
              <div className="text-center">
                <feature.icon className={`w-12 h-12 mx-auto mb-4 ${feature.color}`} />
                <h3 className="text-xl font-semibold text-white mb-3">{feature.title}</h3>
                <p className="text-gray-300 text-sm leading-relaxed">{feature.description}</p>
              </div>
            </Card>
          ))}
        </div>
      </div>
    </section>
  );
}; 