"use client";

import { motion } from 'framer-motion';
import { Card } from '../ui/Card';
import { UserPlus, Gamepad2, Trophy, DollarSign } from 'lucide-react';

export const HowItWorksSection = () => {
  const steps = [
    {
      icon: UserPlus,
      title: 'Sign Up & Connect',
      description: 'Create your account and link your Valorant profile with Tracker.gg for verification.',
      color: 'text-blue-400'
    },
    {
      icon: Gamepad2,
      title: 'Join Tournaments',
      description: 'Browse available tournaments, pay entry fees, and get auto-assigned to matches.',
      color: 'text-purple-400'
    },
    {
      icon: Trophy,
      title: 'Compete & Win',
      description: 'Play your matches, submit results via Tracker.gg links, and advance through brackets.',
      color: 'text-yellow-400'
    },
    {
      icon: DollarSign,
      title: 'Get Paid',
      description: 'Win tournaments and receive instant UPI payments directly to your account.',
      color: 'text-green-400'
    }
  ];

  return (
    <section className="py-20 bg-black/80 backdrop-blur-sm">
      <div className="max-w-7xl mx-auto px-4">
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
          className="text-center mb-16"
        >
          <h2 className="text-4xl md:text-5xl font-bold text-white mb-6">
            How It <span className="bg-gradient-to-r from-purple-400 to-blue-400 bg-clip-text text-transparent">Works</span>
          </h2>
          <p className="text-xl text-gray-300 max-w-3xl mx-auto">
            Get started in minutes and start earning from your Valorant skills
          </p>
        </motion.div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
          {steps.map((step, index) => (
            <motion.div
              key={index}
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: index * 0.2 }}
              viewport={{ once: true }}
              className="relative"
            >
              <Card delay={index * 0.1}>
                <div className="text-center">
                  <div className="relative">
                    <step.icon className={`w-16 h-16 mx-auto mb-4 ${step.color}`} />
                    <div className="absolute -top-2 -right-2 bg-purple-600 text-white rounded-full w-8 h-8 flex items-center justify-center text-sm font-bold">
                      {index + 1}
                    </div>
                  </div>
                  <h3 className="text-xl font-semibold text-white mb-3">{step.title}</h3>
                  <p className="text-gray-300 text-sm leading-relaxed">{step.description}</p>
                </div>
              </Card>
              
              {index < steps.length - 1 && (
                <div className="hidden lg:block absolute top-1/2 -right-4 transform -translate-y-1/2">
                  <div className="w-8 h-0.5 bg-gradient-to-r from-purple-600 to-blue-600"></div>
                </div>
              )}
            </motion.div>
          ))}
        </div>
      </div>
    </section>
  );
}; 