"use client";

import { motion } from 'framer-motion';
import { Button } from '../ui/Button';
import { ArrowRight, Star } from 'lucide-react';

export const CTASection = () => {
  return (
    <section className="py-20 bg-gradient-to-r from-purple-900/80 via-blue-900/80 to-purple-900/80 backdrop-blur-sm">
      <div className="max-w-4xl mx-auto px-4 text-center">
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
        >
          <div className="flex justify-center mb-6">
            {[...Array(5)].map((_, i) => (
              <Star key={i} className="w-8 h-8 text-yellow-400 mx-1" />
            ))}
          </div>
          
          <h2 className="text-4xl md:text-5xl font-bold text-white mb-6">
            Ready to Start <span className="bg-gradient-to-r from-yellow-400 to-orange-400 bg-clip-text text-transparent">Winning</span>?
          </h2>
          
          <p className="text-xl text-gray-200 mb-8 max-w-2xl mx-auto">
            Join thousands of players who are already earning real money from their Valorant skills. 
            Sign up today and get your first tournament win!
          </p>
          
          <div className="flex flex-col sm:flex-row gap-4 justify-center items-center">
            <Button size="lg" onClick={() => console.log('Sign Up clicked')}>
              Sign Up Now
              <ArrowRight className="w-5 h-5 ml-2" />
            </Button>
            <Button variant="outline" size="lg" onClick={() => console.log('Browse Tournaments clicked')}>
              Browse Tournaments
            </Button>
          </div>
          
          <p className="text-sm text-gray-300 mt-6">
            Free to join • Instant payouts • 24/7 tournaments
          </p>
        </motion.div>
      </div>
    </section>
  );
}; 