"use client";

import { motion } from 'framer-motion';
import { Button } from '../ui/Button';
import { Trophy, Users, Zap, Shield } from 'lucide-react';

export const HeroSection = () => {
  const features = [
    { icon: Trophy, text: 'Real Money Payouts', color: 'text-yellow-400' },
    { icon: Users, text: '1v1 & 5v5 Tournaments', color: 'text-blue-400' },
    { icon: Zap, text: 'Auto-Assignment', color: 'text-purple-400' },
    { icon: Shield, text: 'Anti-Abuse System', color: 'text-green-400' },
  ];

  return (
    <section className="relative min-h-screen flex items-center justify-center overflow-hidden">
      <div className="absolute inset-0 bg-gradient-to-br from-black/60 via-purple-900/30 to-black/60" />
      
      <div className="relative z-10 text-center px-4 max-w-6xl mx-auto">
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
        >
          <h1 className="text-5xl md:text-7xl font-bold text-white mb-6">
            <span className="bg-gradient-to-r from-purple-400 to-blue-400 bg-clip-text text-transparent">
              Tournify
            </span>
          </h1>
          
          <p className="text-xl md:text-2xl text-gray-300 mb-8 max-w-3xl mx-auto">
            The ultimate competitive Valorant tournament platform where skill meets reward. 
            Join tournaments, compete for real money, and prove your dominance.
          </p>
          
          <div className="flex flex-col sm:flex-row gap-4 justify-center mb-12">
            <Button size="lg" onClick={() => console.log('Get Started clicked')}>
              Get Started
            </Button>
            <Button variant="outline" size="lg" onClick={() => console.log('Learn More clicked')}>
              Learn More
            </Button>
          </div>
        </motion.div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.3 }}
          className="grid grid-cols-2 md:grid-cols-4 gap-6 max-w-4xl mx-auto"
        >
          {features.map((feature, index) => (
            <motion.div
              key={index}
              className="flex flex-col items-center text-center"
              whileHover={{ scale: 1.05 }}
              transition={{ duration: 0.2 }}
            >
              <feature.icon className={`w-8 h-8 mb-2 ${feature.color}`} />
              <span className="text-sm text-gray-300">{feature.text}</span>
            </motion.div>
          ))}
        </motion.div>
      </div>
    </section>
  );
}; 